body {
  color: black;
}
.exerciseCreatorBody {
  display: flex;
  flex-direction: column;
  max-width: 100%;
  padding: 0;
  /* align-items: center; */
  justify-content: center;
}

.heading {
  display: flex;
  padding: 0;
  justify-content: left;
}

.exercise-top {
  /* margin-left: 20%; */
  display: flex;
  flex-direction: column; /* Align children vertically */
  max-width: 100%;
  padding: 1.25rem;
}
.input {
  margin-left: 13%;
  margin-right: 13%;
  border: 1px solid #ccc;
  margin-bottom: 10px;
  border-radius: 0.5rem;
}

.topp {
  /* border: 1px solid gray; */
  margin-top: -30px;
  /* margin-left: 20%; */
  /* margin-right: 20%; */
  display: flex;
  flex-direction: column; /* Align children vertically */
  max-width: 100%;
  padding: 1.25rem;
  padding-bottom: 1.25rem;
}

.exercise-header {
  display: flex;
  align-items: center;
  padding: 1.25rem;
  padding-bottom: 0;
}

.first {
  display: flex;
  margin-left: -10px;
  margin-top: -10px;
  flex-direction: column; /* Align the text-label pairs vertically */
  width: 100%;
  align-items: start; /* Align items to the start of the container */
}
.first, .levels {
  align-items: start;
  display: flex;
  width: 100%;
}

.levels {
  display: flex;
  /* margin-left: -10px; */
  justify-content: space-between;
  margin-top: 35px;
  height: 60px;
  width: 100%;
  align-items: start; /* Align items to the start of the container */
}



.card-row {
  padding: 0;
  max-height: 2.5rem;
  margin-bottom: 1.5rem;
}
.Text {
  display: flex; /* Enable flex layout */
  /* flex-direction: column; */
  /* align-items: center; Align items vertically */
  width: 100%; /* Full width to fit the parent container */
  margin-bottom: 1.7rem; /* Add some space between the two text-label pairs */
  max-height: 2.5rem !important;
}

.error-message {
  width: 100%;
  /* background-color: red; */
  margin-top: 1.25rem;
  margin-left: 2rem;
}

.top label {
  min-width: 40%; /* Ensure all labels have the same width for alignment */
  margin-right: 0.625rem; /* Space between label and input */
}

/* Add responsiveness for smaller devices */
@media (max-width: 768px) {
  .first {
    width: 100%; /* Adjust width for smaller screens */
  }

  .top label {
    min-width: auto; /* Allow labels to fit content width */
    width: 30%; /* Adjust label width on smaller screens for consistency */
  }

  .top input[type='text'],
  .top select {
    width: 70%; /* Adjust input/select width to fill remaining space */
  }
}

.exercise-error {
  margin-left: 0.625rem;
  color: #ff0000;
  margin-top: 0.3125rem;
  margin-bottom: 1rem;
  font-size: 0.875rem;
  font-weight: bold;
  width: 100%;
}

.MuiMenuItem-root {
  font-family: 'Roboto', sans-serif;
  color: rgb(97, 97, 97);
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.fade-in-message {
  animation: fadeIn 2s;
  text-align: center; /* Center the text horizontally */
  font-size: 1.5rem; /* Larger text */
  color: rgb(97, 97, 97); /* Change the color as per your design */
  margin-top: 1.25rem; /* Add some space above the message */
  margin-left: 27%;
  margin-right: 27%;
}
.exercise-card {
  width: 100%;
  display: flex;
  margin-bottom: 10px;
}

.left {
  width: 90%;
  padding: 15px;
  border: 1px solid gray;
}
.right {
  display: flex;
  align-items: center;
  width: 8%;
  margin-left: 10%;
  /* padding: 1%; */
  text-align: center;
}
.chip-container {
  margin-left: 5%;
  padding: 1%;
  width: 90%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  /* margin: 0; */
  margin-top: 0px;
  margin-bottom: 1%;
  /* padding: 0; */
}



.lvl-btn {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: end;
  height: 40px;
  /* align-items: self-end; */
}
.lvl-btn button {
  margin: 2px;
}
.levell {
  margin-bottom: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 49%;
  /* height: 20px; */
}
.submit-area {
  padding-left: 2%;
  padding-bottom: 0;
  padding-top: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.total-questions {
  margin-top: 10px;
  margin-left: 10px;
  text-align: center;
  display: flex;
  justify-content: start;
  width: 50%;
}

.submit-btn {
  text-align: center;
  width: 45%;
  display: flex;
  flex-wrap: wrap;
  align-items: end;
  justify-content: end;
  margin-bottom: 10px;
  margin-right: 20px;
}

.submit-btn button {
  margin: 0;
}

.output-area {
  border: 0.125rem solid #000000;
  max-width: 80%;
  margin-left: 13%;
  margin-bottom: 2%;
  margin-right: 13%;
  padding: 2rem;
}

.chips {
  /* margin-top: 2%; */
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.loading-dots {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.7); /* Semi-transparent black */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999; /* Ensure it's above other content */
}
